.forestfires-bg {
    width: 100%;
    height: 100%;
    background: url('../../assets/fire.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    transition: all 1s;
    opacity:60%;

}

.forestfires-bg:hover {
    transform: scale(1.2);
    opacity:40%;
}