.oceans-bg {
    width: 100%;
    height: 100%;
    background: url('../../images/ocean-bg.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    transition: all 1s;
    opacity:100%;

}

.oceans-bg:hover {
    transform: scale(1.2);
    opacity:80%;
}