 .arctic-bg {
    width: 100%;
    height: 100%;
    background: url('../../assets/Icesheets_bg.jpg');
    background-position: right;
    background-size: 350px;
    background-repeat: no-repeat;
    transition: all 1s;
    opacity:90%;

}

.arctic-bg:hover {
    transform: scale(1.2);
    opacity:100%;
}