.flooding-bg{
    width: 100%;
    height: 100%;
    background: url('../../assets/flooding.jpg');
    background-position: bottom;
    background-size: 460px;
    background-repeat: no-repeat;
    transition: all 1s;
    opacity:90%;

}

.flooding-bg:hover {
    transform: scale(1.2);
    opacity:100%;
}