.coral01-bg{
    width: 100%;
    height: 100%;
    background: url('../../assets/coral.jpg');
    background-position: right;
    background-size: 300px;
    background-repeat: no-repeat;
    transition: all 1s;
    opacity:90%;

}

.coral01-bg:hover {
    transform: scale(1.2);
    opacity:100%;
}