.airbg{
	position: fixed;
	width: 100%;
	height:100%;
	
	background-image: radial-gradient(circle at top,#5077A7, #044491);
	z-index: -100;
	top:0;
	left:0;
}

h5{
	font-family: roboto;
	font-weight: 300;
	font-size: 3em;
	color:white;
	opacity: 15%;
	position: absolute;
  top: -120px;
  left: 200px;
   
   right: 0;
}

@media (max-width: 700px){
h5{
    font-size: 3em;
    top: -20px;
    left: 30px;
  }
}
