.side-menu {
  height: 100%;
  background: rgba(0, 0, 0, 1);
  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 170px;
  z-index: 200;
  justify-content: space-between;
}

.nav{
 justify-content: space-between;
}
.spacer{
  margin-top:50px;
}
.side-menu ul{
	height:100%;
	list-style: none;
	display:flex;
	flex-direction: column;
	justify-content: center;
  padding:0px;
  justify-content: space-between;
  

}

.side-menu li{
	
}

.side-menu a {
	color:gray;
	text-decoration: none;
	font-size: 1.2rem;
}

.side-menu a:hover,
.side-menu a:active{
	color:#ffffff;
}

.logo{
  width: 55px;
  margin-left: 55px;
  margin-top: 25px;
  opacity: 0.8;
}
.ico{
  width:20px;
  margin-left: 10px;
  margin-right:10px;
}

.onpage{
  border: none;
    background-color: #780A38; 
    border: none;
    color: white;
    vertical-align: middle;
    text-align: middle;
    text-decoration: none;
    font-family: roboto;
    font-weight: 400;
    padding-top: 10px;
    padding-bottom: 10px;
    flex-direction: row;
    font-size: .7em;
    width:100%;
    display: flex;
    align-items: center;
    justify-content: left;
}

.button{
  opacity: 60%;
    border: none;
    background-color: rgba(0,0,0,1); 
    border: none;
    color: white;
    vertical-align: middle;
    text-align: middle;
    text-decoration: none;
    font-family: roboto;
    font-weight: 400;

    padding-top: 10px;
    padding-bottom: 10px;
    
    
    flex-direction: row;
    
    font-size: .7em;
    width:100%;

    display: flex;
    align-items: center;
    justify-content: left;
}

.button:active, 
.button:hover{
  opacity: 100%;
  background-color: #780A38;
  transition-duration: .5s;
}
.wrapper{
vertical-align: middle;
}
h2{
  color:white;
  font-family: roboto;
  font-weight: 300;
  justify-content: left;
  font-size:0.5rem;
  margin-bottom:2px;
  margin-left:50px;

}

span{
  color:#ed146f;
  font-family: roboto;
  font-weight: 500;
  justify-content: left;
  font-size:0..9rem;
  margin:0px;

}
@media (max-width: 768px){
  .side-menu{
    display:none;
  }
}
